const { algoliasearch, instantsearch } = window;

const searchClient = algoliasearch(
  'latency',
  '56f24e4276091e774e8157fe4b8b11f6'
);

const search = instantsearch({
  indexName: 'movies',
  searchClient,
});

search.addWidgets([
  instantsearch.widgets.searchBox({
    container: '#searchbox',
  }),
  instantsearch.widgets.hits({
    container: '#hits',
    templates: {
      item: `
<article>
  <h1>{{#helpers.highlight}}{ "attribute": "title" }{{/helpers.highlight}}</h1>
  <p>{{#helpers.highlight}}{ "attribute": "year" }{{/helpers.highlight}}</p>
  <p>{{#helpers.highlight}}{ "attribute": "actors" }{{/helpers.highlight}}</p>
</article>
`,
    },
  }),
  instantsearch.widgets.configure({
    facets: ['*'],
    maxValuesPerFacet: 20,
  }),
  instantsearch.widgets.dynamicWidgets({
    container: '#dynamic-widgets',
    fallbackWidget({ container, attribute }) {
      return instantsearch.widgets.refinementList({
        container,
        attribute,
      });
    },
    widgets: [
      container =>
        instantsearch.widgets.refinementList({
          container,
          attribute: 'year',
        }),
      container =>
        instantsearch.widgets.refinementList({
          container,
          attribute: 'genre',
        }),
      container =>
        instantsearch.widgets.refinementList({
          container,
          attribute: 'actors',
        }),
      container =>
        instantsearch.widgets.refinementList({
          container,
          attribute: 'rating',
        }),
      container =>
        instantsearch.widgets.refinementList({
          container,
          attribute: 'actor_facets',
        }),
    ],
  }),
  instantsearch.widgets.pagination({
    container: '#pagination',
  }),
  instantsearch.widgets.clearRefinements({
    container: '#clear-refinements',
  }),

  instantsearch.widgets.refinementList({
    container: '#actors-list',
    attribute: 'actors',
    searchable: true,
    operator: 'and',
  }),

  instantsearch.widgets.configure({
    hitsPerPage: 8,
  }),
]);

search.start();
